<script>

export default {
    name: "Cv",
    data() {
        return {
            hw_logo: require('../assets/img/cv_icons/hw_logo_mini.png'),
            dryckeskonsult_logo_white: require('../assets/img/cv_icons/dryckeskonsult_logo_white.png'),
