            coop_logo: require('../assets/img/cv_icons/Coop_logo.png')
        }
    },
    components: {

    },
    mounted() {
        // Element med klass gb-vue-timeline-update__information & gb-vue-timeline-update__thumbnail har dataelement \/
        // data-aos="fade-zoom-in" data-aos-offset="200" data-aos-easing="ease-in-sine" data-aos-duration="1000"

        const AOS = require('aos');

        const animatedElements = document.querySelectorAll(
            '.gb-vue-timeline-update__right,' + 
            '.gb-vue-timeline-update__bullet'
            )

        // fade-right
        const timeSinceWorkedText = document.querySelectorAll('.gb-vue-timeline-update__ago')

        timeSinceWorkedText.forEach(function(e) {
            e.setAttribute('data-aos', 'fade-right')
            e.setAttribute('data-aos-duration', '1000')
        })

        console.log("LÄNGD: " + animatedElements.length)


        animatedElements.forEach(function(e, i) {
            console.log(i)

            // Skippar senaste jobbet. Högst upp.
            if(i > 1) {
                e.setAttribute('data-aos', 'fade-up')
                e.setAttribute('data-aos-offset', '90')
                e.setAttribute('data-aos-anchor-placement', 'top-bottom')
                e.setAttribute('data-aos-duration', '1000')                
            }
        })



        document.addEventListener('aos:in:super-duper', ({ detail }) => {
            console.log('in!', detail);
        });

                AOS.refreshHard();
    }
}
</script>

